import React, { useState, useEffect } from "react";
import { Container, Col, Row, Table, Form } from "react-bootstrap";
import { toast } from "react-toastify";
import ReportsCard from "./ReportsCard";
import ReportProductList from "./ReportProductList";
import { useNavigate } from "react-router-dom";

const Reports = () => {
  const [orders, setOrders] = useState([]);
  const [pendingOrders, setPendingOrders] = useState([]);
  const [products, setProducts] = useState([]);
  const [lowStockProducts, setLowStockProducts] = useState([]);
  const [currentSales, setCurrentSales] = useState(0);
  const [monthlySales, setMonthlySales] = useState(0);
  const [totalSales, setTotalSales] = useState(0);
  const [enrouteOrders, setEnrouteOrders] = useState([]);
  const baseURL = process.env.REACT_APP_API_URL || '';
  const navigate = useNavigate();

  useEffect(() => {
    fetchOrders();
    fetchPendingOrders();
    fetchProducts();
    fetchLowStockProducts();
    fetchCurrentSales();
    fetchMonthlySales();
    fetchTotalSales();
    fetchEnrouteOrders();
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await fetch(`${baseURL}/api/orders/all`);
      if (!response.ok) throw new Error("Failed to fetch orders");
      const data = await response.json();
      setOrders(data);
    } catch (error) {
      toast.error("Failed to fetch orders");
    }
  };

  const fetchPendingOrders = async () => {
    try {
      const response = await fetch(
       `${baseURL}/api/orders/status/pending`
      );
      if (!response.ok) throw new Error("Failed to fetch pending orders");
      const data = await response.json();
      console.log(data);
      setPendingOrders(data);
    } catch (error) {
      toast.error("Failed to fetch pending orders");
    }
  };

  const fetchEnrouteOrders = async () => {
    try {
      const response = await fetch(
        `${baseURL}/api/orders/status/en-route`
      );
      if (!response.ok) throw new Error("Failed to fetch en-route orders");
      const data = await response.json();
      console.log("enroute" + data.length);
      setEnrouteOrders(data);
    } catch (error) {
      toast.error("Failed to fetch  en-route orders");
    }
  };

  const fetchProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/all`);
      if (!response.ok) throw new Error("Failed to fetch products");
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      toast.error("Failed to fetch products");
    }
  };

  const fetchLowStockProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/low`);
      if (!response.ok) throw new Error("Failed to fetch low stock products");
      const data = await response.json();
      setLowStockProducts(data);
    } catch (error) {
      toast.error("Failed to fetch low stock products");
    }
  };

  const fetchCurrentSales = async () => {
    try {
      const response = await fetch(
        `${baseURL}/api/reports/current-date`
      );
      if (!response.ok) throw new Error("Failed to fetch current sales");
      const data = await response.json();
      setCurrentSales(data);
    } catch (error) {
      toast.error("Failed to fetch current sales");
    }
  };

  const fetchMonthlySales = async () => {
    try {
      const response = await fetch(`${baseURL}/api/reports/monthly`);
      if (!response.ok) throw new Error("Failed to fetch monthly sales");
      const data = await response.json();
      setMonthlySales(data);
    } catch (error) {
      toast.error("Failed to fetch monthly sales");
    }
  };

  const fetchTotalSales = async () => {
    try {
      const response = await fetch(`${baseURL}/api/reports/total`);
      if (!response.ok) throw new Error("Failed to fetch total sales");
      const data = await response.json();
      setTotalSales(data);
    } catch (error) {
      toast.error("Failed to fetch total sales");
    }
  };
  console.log(pendingOrders);

  const navigateToReports = () => {
    navigate("/admin/reports");
  };

  return (
    <Container className="my-3">
      <div className="border p-4 bg-light rounded">
      <h1 style={{ cursor: "pointer" }} onClick={navigateToReports}>Reports</h1>
        <Row className="mtop-2">
        <Col xs={4}>
            <Row>
              <Col>
                <div className="border p-4 bg-light rounded mb-2"><h2>Pending Orders: {pendingOrders.length}</h2></div>
              </Col>
            </Row>
            <Row>
              <Col>
              <div className="border p-4 bg-light rounded mb-2"><h2>En-route Orders: {enrouteOrders.length}</h2></div>
              </Col>
            </Row>
          </Col>
          <ReportsCard
            currentSales={currentSales}
            monthlySales={monthlySales}
            totalSales={totalSales}
          />
          <Col xs={4}>
          <ReportProductList />
          </Col>
        </Row>
      </div>
    </Container>
  );
};

export default Reports;
