import React, { useState, useEffect } from "react";
import { Container, Col, Row, Table, Form, Button } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import { toast } from "react-toastify";

const OrderList = () => {
  const [orders, setOrders] = useState([]);
  const [customers, setCustomers] = useState({});
  const [status, setStatus] = useState("Pending"); // Default status
  const navigate = useNavigate();
  const location = useLocation();
  const baseURL = process.env.REACT_APP_API_URL || '';

  useEffect(() => {
    fetchOrders(status);
    fetchCustomers(); // Fetch customers when component mounts
  }, [status]);

  const fetchOrders = async (status) => {
    try {
      const response = await fetch(
        `${baseURL}/api/orders/status/${status}`
      );
      if (!response.ok) {
        throw new Error("Error fetching orders");
      }
      const data = await response.json();
      setOrders(data);
    } catch (error) {
      console.error("Error fetching orders:", error);
    }
  };

  const fetchCustomers = async () => {
    try {
      const response = await fetch(`${baseURL}/api/customer/all`);
      if (!response.ok) {
        throw new Error("Error fetching customers");
      }
      const data = await response.json();
      // Assuming data is an array of customers with ids and names
      const customersMap = {};
      data.forEach((customer) => {
        customersMap[customer.id] = customer.name;
      });
      setCustomers(customersMap);
    } catch (error) {
      console.error("Error fetching customers:", error);
    }
  };

  const navigateToEditOrder = (id) => {
    navigate(`/admin/orders/edit/${id}`); // Pass the id to the URL
  };

  const navigateToOrderScreen = () => {
    navigate(`/admin/orders`); 
  };

  const handleDelete = async (id) => {
    const confirmed = window.confirm(
      `Are you sure you want to delete the order "${id}"?`
    );
    if (confirmed) {
      try {
        const response = await fetch(`${baseURL}/api/orders/${id}`, {
          method: "DELETE",
        });
        if (!response.ok) {
          throw new Error(`Failed to delete order with id ${id}`);
        }
        setOrders(orders.filter((order) => order.id !== id));
        toast.success(`Order "${id}" deleted successfully!`);
      } catch (error) {
        console.error("Error deleting order:", error);
        toast.error("Failed to delete order.");
      }
    }
  };

  const getCustomerName = (customerId) => {
    return customers[customerId] || "Unknown Customer";
  };


  const handleAddOrder = (id) => {
    navigate(`/admin/orders/add`); // Pass the id to the URL
  };

  const navigateToHome = () => {
    navigate("/admin");
  };

  return (
    <>
      <Container className="mt-4">
        <Row>
          <Col>
            <div className="border p-4 bg-light rounded">
              <Row className="align-items-center mb-3">
                <Col>
                <h1 style={{ cursor: "pointer" }} onClick={navigateToOrderScreen}>Order List</h1>
                </Col>
                <Col>
                  <Form.Select 
                    value={status}
                    onChange={(e) => setStatus(e.target.value)}
                  >
                    <option value="Pending">Pending</option>
                    <option value="En-route">En-route</option>
                    <option value="Delivered">Delivered</option>
                  </Form.Select>
                </Col>
                <Col className="text-end">
                  {location.pathname === "/admin/orders" && (
                    <>
                      <Button variant="primary" onClick={handleAddOrder}>
                        Add Order
                      </Button>
                      <Button
                        variant="primary"
                        onClick={navigateToHome}
                        className="mx-2"
                      >
                        Return to home
                      </Button>
                    </>
                  )}
                </Col>
              </Row>
              <Row>
                <Col>
                  <Table striped hover responsive bordered className="table-sm">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Customer</th>
                        <th>Address</th>
                        <th>Status</th>
                        <th>Last Updated</th>
                        {location.pathname === "/admin/orders" && (
                          <th>Action</th>
                        )}
                      </tr>
                    </thead>
                    <tbody>
                      {orders.map((order) => (
                        <tr key={order.id}>
                          <td>{order.id}</td>
                          <td>{getCustomerName(order.customerId)}</td>
                          <td>{order.address}</td>
                          <td>{order.status}</td>
                          <td>{order.lastUpdated}</td>
                          {location.pathname === "/admin/orders" && (
                            <>
                            <td>
                              <Button
                                variant="primary"
                                onClick={() => navigateToEditOrder(order.id)}
                                className="me-2"
                              >
                                Edit
                              </Button>
                              </td>
                              <td>
                              <Button
                                variant="danger"
                                onClick={() => handleDelete(order.id)}
                              >
                                Delete
                              </Button>
                            </td>
                            </>
                          )}
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default OrderList;
