import React, { useState, useEffect } from "react";
import { Container, Col, Row, Button, Table } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import {toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const ProductList = () => {
  const [products, setProducts] = useState([]);
  const navigate = useNavigate();
  const location = useLocation();
  const baseURL = process.env.REACT_APP_API_URL || '';

  const clickProductHandler = () => {
    navigate("/admin/products/add");
  };

  const navigateToProduct = () => {
    navigate("/admin/products");
  };

  const navigateToEditProduct = (id) => {
    navigate(`/admin/products/edit/${id}`); // Pass the id to the URL
  };

  const navigateToHome = (id) => {
    navigate("/admin");
  };


  const fetchProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/all`);
      if (!response.ok) {
        throw new Error("Failed to fetch products");
      }
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  // Call fetchProducts when the component mounts and when you want to refetch
  useEffect(() => {
    fetchProducts();
  }, []);
  const handleDelete = async (id, name) => {
    const confirmed = window.confirm(
      `Are you sure you want to delete the product "${name}"?`
    );
    if (confirmed) {
      try {
        const response = await fetch(
          `${baseURL}api/product/${id}`,
          {
            method: "DELETE",
          }
        );
        if (!response.ok) {
          throw new Error(`Failed to delete product with id ${id}`);
        }
        setProducts(products.filter((product) => product.id !== id));
        toast.success(`Product "${name}" deleted successfully!`);
      } catch (error) {
        console.error("Error deleting product:", error);
        toast.error("Failed to delete product.");
      }
    }
  };

  return (
    <>
      <Container className="mt-4">
        <Row>
          <Col>
            <div className="border p-4 bg-light rounded">
              <Row className="align-items-center mb-3">
                <Col>
                  <h1 style={{ cursor: "pointer" }} onClick={navigateToProduct}>
                    Products{" "}
                  </h1>
                </Col>
                <Col className="text-end">
                  {location.pathname === "/admin/products" && (
                    <Button variant="primary" onClick={clickProductHandler}>
                      Add Product
                    </Button>
                  )}
                       {location.pathname === "/admin/products" && (
                    <Button variant="primary" onClick={navigateToHome} className="mx-2">
                      Return to home
                    </Button>
                  )}
                </Col>
              </Row>
              <Row>
                <Col>
                  <Table striped hover responsive bordered className="table-sm">
                    <thead>
                      <tr>
                        <th>Image</th>
                        <th>ID</th>
                        <th>Name</th>
                        <th>Quantity</th>
                        <th>Price</th>
                        <th>Bought Price</th>
                        {location.pathname === "/admin/products" && <th>Action</th>}
                        </tr>
                    </thead>
                    <tbody>
                      {products.map((product) => (
                        <tr key={product.id}>
                          <td>
                            <img
                              src={`${baseURL}/api/product/image/${product.image}`}
                              alt={product.name}
                              style={{ maxWidth: "50px", maxHeight: "50px" }}
                            />
                          </td>
                          <td>{product.id}</td>
                          <td>{product.name}</td>
                          <td>{product.quantity.toString()}</td>
                          <td>{product.price.toString()}</td>
                          <td>{product.buyPrice.toString()}</td>
                          {location.pathname === "/admin/products" && (
                            <>
                              <td>
                              <Button
                                variant="primary"
                                onClick={() => navigateToEditProduct(product.id)}
                                className="me-2"
                              >
                                Edit
                                </Button>
                              </td>
                              <td> <Button
                                variant="danger"
                                onClick={() =>  handleDelete(product.id, product.name)}
                              >
                                Delete
                              </Button>
                              </td>
                            </>
                          )}
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default ProductList;
