import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../AuthContext";

function Header() {
  const navigate = useNavigate();
  const { logout } = useAuth();

  const clickProductHandler = () => {
    navigate("/admin/products");
  };

  const clickOrderHandler = () => {
    navigate("/admin/orders");
  };

  
  const clickReportsHandler = () => {
    navigate("/admin/reports");
  };

  
  const clickCustomerHandler = () => {
    navigate("/admin/customers");
  };


  const clickHomeHandler = () => {
    navigate("/admin/");
  };

  const handleLogout = () => {
    logout(); // Call the logout function from AuthContext
    navigate('/'); // Redirect to the login or home page
  };
  return (
    <>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={clickHomeHandler}   style={{ cursor: "pointer" }}>Inventory Management System</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              {/* Updated class here */}
              <Nav.Link href="#home">Inventory</Nav.Link>
              <NavDropdown title="Admin" id="basic-nav-dropdown">
                <NavDropdown.Item onClick={clickReportsHandler}>Reports</NavDropdown.Item>
                <NavDropdown.Item onClick={clickProductHandler}>Products</NavDropdown.Item>
                <NavDropdown.Item onClick={clickOrderHandler}>Orders</NavDropdown.Item>
                <NavDropdown.Item onClick={clickCustomerHandler}>Customer</NavDropdown.Item>
                <NavDropdown.Item onClick={clickCustomerHandler}>User</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={handleLogout}>Logout</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;
