import React, { useState } from "react";
import { Container, Button, Form } from "react-bootstrap";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";

const AddCustomerScreen = () => {
  const [customerName, setCustomerName] = useState("");
  const [customerAddress, setCustomerAddress] = useState("");
  const navigate = useNavigate();
  const baseURL = process.env.REACT_APP_API_URL || '';

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Check if any field is empty
    if (!customerName || !customerAddress) {
      toast.error("Please fill in all fields");
      return;
    }

    const customerData = {
      name: customerName,
      address: customerAddress,
    };

    try {
      const response = await fetch(`${baseURL}/api/customer/add`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(customerData),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log("Customer added successfully:", data);

      // Reset form fields
      setCustomerName("");
      setCustomerAddress("");

      // Show success toast
      toast.success("Customer added successfully");

      // Redirect to home or customer list
      navigate("/admin/customers");
    } catch (error) {
      console.error("Error adding customer:", error);
      toast.error("Error adding customer. Please try again.");
    }
  };

  const handleBackToHome = (e) => {
    e.preventDefault();
    navigate("/admin/customers");
  };

  return (
    <Container className="mt-4">
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="customerName">
          <Form.Label>Customer Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter customer name"
            value={customerName}
            onChange={(e) => setCustomerName(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="customerAddress">
          <Form.Label>Customer Address</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter customer address"
            value={customerAddress}
            onChange={(e) => setCustomerAddress(e.target.value)}
          />
        </Form.Group>
        <Button variant="primary" type="submit" className="my-4">
          Add Customer
        </Button>
        <Button variant="primary" type="" className="text-end mx-4" onClick={handleBackToHome}>
          Back to Customers
        </Button>
      </Form>
      <ToastContainer />
    </Container>
  );
};

export default AddCustomerScreen;