import React, { useState, useEffect } from "react";
import { Col, Card } from "react-bootstrap";

const ReportsCard = ({currentSales, monthlySales, totalSales }) => {
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000); // Update every second

    return () => clearInterval(intervalId); // Clean up interval on component unmount
  }, []);

  return (
    <Col xs={4} className="left-column">
      <Card>
        <Card.Header>Reports</Card.Header>
        <Card.Body>
          <Card.Text className="text-line">Current date/time: {currentTime.toLocaleString()}</Card.Text>
          <Card.Text className="text-line">Sales current date: {currentSales}</Card.Text>
          <Card.Text className="text-line">Sales monthly: {monthlySales}</Card.Text>
          <Card.Text className="text-line">Total sales: {totalSales}</Card.Text>
        </Card.Body>
      </Card>
    </Col>
  );
};

export default ReportsCard;