import React, { useState, useEffect } from "react";
import { Container, Form, Button } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";

const EditCustomerScreen = () => {
    const { id } = useParams(); // Get the customer ID from the URL params
    const [customer, setCustomer] = useState({
      name: "",
      contactNumber: "",
      address: "",
    });
    const navigate = useNavigate();
    const baseURL = process.env.REACT_APP_API_URL || '';
  
    useEffect(() => {
      const fetchCustomer = async () => {
        try {
          const response = await fetch(`${baseURL}/api/customer/${id}`);
          if (!response.ok) {
            throw new Error("Failed to fetch customer");
          }
          const data = await response.json();
          setCustomer(data);
        } catch (error) {
          console.error("Error fetching customer:", error);
        }
      };
  
      fetchCustomer();
    }, [id]);
  
    const handleChange = (e) => {
      const { name, value } = e.target;
      setCustomer({ ...customer, [name]: value });
    };
  
    const handleSubmit = async (e) => {
      e.preventDefault();
      try {
        const response = await fetch(
          `${baseURL}/api/customer/update/${id}`,
          {
            method: "PUT",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(customer),
          }
        );
    
        if (!response.ok) {
          throw new Error(`Failed to update customer: ${response.status}`);
        }
    
        const data = await response.json();
        console.log("Customer updated successfully:", data);
        toast.success("Customer updated successfully");
        navigate("/admin/customers");
      } catch (error) {
        console.error("Error updating customer:", error);
        toast.error("Error updating customer");
      }
    };

    const handleBackToHome = (e) => {
        e.preventDefault();
        navigate("/admin/customers");
      };
  
  return (
    <Container className="mt-4">
      <h1>Edit Customer</h1>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="customerName">
          <Form.Label>Customer Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter customer name"
            name="name"
            value={customer.name}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="customerName">
          <Form.Label>Contact Number</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter customer name"
            name="contactNumber"
            value={customer.contactNumber}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="customerAddress">
          <Form.Label>Customer Address</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter customer address"
            name="address"
            value={customer.address}
            onChange={handleChange}
          />
        </Form.Group>
        <Button variant="primary" type="submit" className="my-2">
          Update Customer
        </Button>
        <Button variant="primary" className="text-end mx-4" onClick={handleBackToHome}>
          Back to Customers
        </Button>
      </Form>
      <ToastContainer /> {/* Toast container */}
    </Container>
  );
};

export default EditCustomerScreen;