import React, { useState, useEffect } from "react";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import { useParams, useNavigate } from "react-router-dom";

const EditOrderScreen = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [selectedCustomer, setSelectedCustomer] = useState(null);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [selectedProductId, setSelectedProductId] = useState("");
  const [orderItems, setOrderItems] = useState([]);
  const [address, setAddress] = useState("");
  const [customers, setCustomers] = useState([]);
  const [products, setProducts] = useState([]);
  const [selectedQuantity, setSelectedQuantity] = useState(1);
  const [orderDetails, setOrderDetails] = useState(null);
  const [status, setStatus] = useState("Pending"); // Default status
  const [customerNumber, setCustomerNumber] = useState("");
  const baseURL = process.env.REACT_APP_API_URL || '';

  useEffect(() => {
    fetchOrderDetails();
    fetchCustomers();
    fetchProducts();
  }, []);
  
  useEffect(() => {
    if (orderDetails) {
      // Set order items if orderDetails contains items
      if (orderDetails.items) {
        setOrderItems(orderDetails.items);
      }
      console.log(orderItems)
      // Fetch customer details based on customerId from orderDetails
      const fetchCustomerDetails = async () => {
        try {
          const response = await fetch(
            `${baseURL}/api/customer/${orderDetails.customerId}`
          );
          if (!response.ok) {
            throw new Error("Error fetching customer details");
          }
          const data = await response.json();
          setSelectedCustomer(data);
          setCustomerNumber(data.contactNumber); // Set selected customer
        } catch (error) {
          console.error("Error fetching customer details:", error);
        }
      };
      fetchCustomerDetails();
    }
  }, [orderDetails]);

  const fetchOrderDetails = async () => {
    try {
      const response = await fetch(`${baseURL}/api/orders/${id}`);
      if (!response.ok) {
        throw new Error("Error fetching order details");
      }
      const data = await response.json();
      setSelectedCustomer(data.customer);
      setOrderItems(data.items);
      setAddress(data.address);
      setOrderDetails(data); // Set order details
      setStatus(data.status); // Set status from order details
    } catch (error) {
      console.error("Error fetching order details:", error);
    }
  };

  const fetchCustomers = async () => {
    try {
      const response = await fetch(`${baseURL}/api/customer/all`);
      if (!response.ok) {
        throw new Error("Error fetching customers");
      }
      const data = await response.json();
      setCustomers(data);
    } catch (error) {
      console.error("Error fetching customers:", error);
    }
  };

  const fetchProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/all`);
      if (!response.ok) {
        throw new Error("Error fetching products");
      }
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  const handleAddProduct = () => {
    if (selectedProductId && selectedQuantity > 0) {
      const selectedProduct = products.find(
        (product) => product.id === parseInt(selectedProductId)
      );
      if (selectedProduct) {
        const existingItemIndex = orderItems.findIndex(
          (item) => item.productId === selectedProduct.id
        );
        if (existingItemIndex !== -1) {
          const updatedOrderItems = [...orderItems];
          updatedOrderItems[existingItemIndex].quantity += selectedQuantity;
          updatedOrderItems[existingItemIndex].totalPrice =
            updatedOrderItems[existingItemIndex].quantity *
            selectedProduct.price;
          setOrderItems(updatedOrderItems);
        } else {
          const newItem = {
            productId: selectedProduct.id,
            quantity: selectedQuantity,
            product: selectedProduct,
            totalPrice: selectedProduct.price * selectedQuantity,
          };
          setOrderItems([...orderItems, newItem]);
        }
        setSelectedProduct(null);
        setSelectedProductId("");
        setSelectedQuantity(1);
      }
    }
  };

  const handleRemoveItem = (index) => {
    setOrderItems(orderItems.filter((_, i) => i !== index));
  };

  const handleSubmitOrder = async () => {
    try {
      const items = orderItems.map((item) => ({
        productId: item.productId,
        quantity: item.quantity,
      }));
  
      const order = {
        id: orderDetails.id, // Use the order ID from orderDetails
        customerId: selectedCustomer.id,
        items: items,
        address: address,
        status: status, // Use the selected status
        lastUpdated: new Date().toISOString(),
      };
  
      console.log(order);
  
      const response = await fetch(
        `${baseURL}/api/orders/update/${order.id}`, // Use order.id
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(order),
        }
      );
  
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(`Error updating order: ${errorData.message}`);
      }
  
      const data = await response.json();
      console.log("Order updated successfully:", data);
      navigate("/admin/orders"); // Redirect to the orders list page
    } catch (error) {
      console.error("Error updating order:", error);
    }
  };

  const handleBackToHome = (e) => {
    e.preventDefault();
    navigate("/admin/orders");
  };

  return (
    <div>
      <h1>Edit Order</h1>
      <Form>
        <Row className="mb-3">
          <Col sm={6}>
            <Form.Group controlId="customerSelect">
              <Form.Label>Select Customer:</Form.Label>
              <Form.Control
                as="select"
                value={selectedCustomer ? selectedCustomer.id : ""}
                onChange={(e) => {
                  const customerId = parseInt(e.target.value);
                  const customer = customers.find((c) => c.id === customerId);
                  setSelectedCustomer(customer);
                }}
              >
                <option value="">Select Customer</option>
                {customers.map((customer) => (
                  <option key={customer.id} value={customer.id}>
                    {customer.name}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            {selectedCustomer && (
              <Form.Text className="text-muted">
                Customer Number: {customerNumber}
              </Form.Text>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col sm={6}>
            <Form.Group controlId="address">
              <Form.Label>Address:</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Address"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col sm={6}>
            <Form.Group controlId="statusSelect">
              <Form.Label>Select Status:</Form.Label>
              <Form.Control
                as="select"
                value={status}
                onChange={(e) => setStatus(e.target.value)}
              >
                <option value="Pending">Pending</option>
                <option value="Enroute">En-route</option>
                <option value="Delivered">Delivered</option>
              </Form.Control>
            </Form.Group>
          </Col>
        </Row>

        <Row className="mb-3 align-items-end">
          <Col sm={6}>
            <Form.Group controlId="productSelect">
              <Form.Label>Select Product:</Form.Label>
              <div className="d-flex">
                <Form.Control
                  as="select"
                  value={selectedProductId}
                  onChange={(e) => {
                    const productId = e.target.value;
                    if (productId) {
                      const product = products.find(
                        (product) => product.id === parseInt(productId)
                      );
                      setSelectedProduct(product);
                      setSelectedProductId(productId);
                    } else {
                      setSelectedProduct(null);
                      setSelectedProductId("");
                    }
                  }}
                >
                  <option value="">Select Product</option>
                  {products.map((product) => (
                    <option key={product.id} value={product.id}>
                      {product.name}
                    </option>
                  ))}
                </Form.Control>
                <Form.Control
                  type="number"
                  min="1"
                  max={selectedProduct ? selectedProduct.quantity : 1}
                  value={selectedProduct ? selectedQuantity : ""}
                  onChange={(e) =>
                    setSelectedQuantity(parseInt(e.target.value))
                  }
                  placeholder={selectedProduct ? "" : "Select product first"}
                  disabled={!selectedProduct}
                  className="ms-2 me-2"
                />
                <Form.Text className="text-muted">
                  {selectedProduct ? `Max: ${selectedProduct.quantity}` : ""}
                </Form.Text>
              </div>
            </Form.Group>
          </Col>
          <Col>
            <Button
              variant="primary"
              onClick={handleAddProduct}
              disabled={!selectedProductId || (selectedProduct && selectedProduct.quantity <= 0)}
            >
              Add Product
            </Button>
          </Col>
        </Row>

        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Product</th>
              <th>Quantity</th>
              <th>Total Price</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {orderItems.map((item, index) => (
              <tr key={index}>
                <td>
                  {products.find((product) => product.id === item.productId)
                    ?.name || ""}
                </td>
                <td>{item.quantity}</td>
                <td>{item.totalPrice}</td>
                <td>
                  <Button
                    variant="danger"
                    onClick={() => handleRemoveItem(index)}
                  >
                    Remove
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        <Row className="mb-3">
          <Col sm={6}>
            <Button variant="primary" onClick={handleSubmitOrder} className="mx-2">
              Save
            </Button>
            <Button variant="secondary" onClick={handleBackToHome}>
              Go Back to Orders Screen
            </Button>
          </Col>
        </Row>
      </Form>
    </div>
  );
};
export default EditOrderScreen;
