import React, { useState } from "react"; // Import useState
import ProductList from "../components/ProductList";
import FormContainer from "../components/FormContainer";
import { Container, Button, Form } from "react-bootstrap"; // Removed unnecessary imports
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";

const AddProductScreen = () => {
    const [productName, setProductName] = useState("");
    const [quantity, setQuantity] = useState("");
    const [price, setPrice] = useState("");
    const [boughtPrice, setBoughtPrice] = useState("");
    const [image, setImage] = useState(null);
    const navigate = useNavigate();
    const baseURL = process.env.REACT_APP_API_URL || '';
  
    const handleSubmit = async (e) => {
      e.preventDefault();
  
      // Check if any field is empty
      if (!productName || !quantity || !price || !image) {
        toast.error("Please fill in all fields");
        return;
      }
  
      const formData = new FormData();
      formData.append(
        "product",
        JSON.stringify({ name: productName, quantity: Number(quantity), price: Number(price), buyPrice: Number(boughtPrice) })
      );
      formData.append("image", image);
  
      try {
        const response = await fetch(`${baseURL}/api/product/add`, {
          method: "POST",
          body: formData,
        });
  
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
  
        const data = await response.json();
        console.log("Product added successfully:", data);
  
        // Reset form fields
        setProductName("");
        setQuantity("");
        setPrice("");
        setImage(null);
  
        // Show success toast
        toast.success("Product added successfully");
  
        // Redirect to product list or a success page
        navigate("/admin");
      } catch (error) {
        console.error("Error adding product:", error);
        toast.error("Error adding product. Please try again.");
      }
    };
  
    const handleBackToHome = (e) => {
      e.preventDefault();
      navigate("/admin/products");
    };

  return (
    <Container className="mt-4">
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="productName">
          <Form.Label>Product Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter product name"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="quantity">
          <Form.Label>Quantity</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter quantity"
            value={quantity}
            onChange={(e) => setQuantity(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="price">
          <Form.Label>Price</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter price"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="price">
          <Form.Label>Bought Price</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter bought price"
            value={boughtPrice}
            onChange={(e) => setBoughtPrice(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="image">
          <Form.Label>Image</Form.Label>
          <Form.Control
            type="file"
            onChange={(e) => setImage(e.target.files[0])}
          />
        </Form.Group>
        <Button variant="primary" type="submit" className="my-4">
          Add Product
        </Button>
        <Button variant="primary" type="" className="text-end mx-4" onClick={handleBackToHome}>
          back to home
        </Button>
      </Form>
      <ToastContainer />
    </Container>
  );
};

export default AddProductScreen;
