import React, { useState, useEffect } from "react";
import { Container, Col, Row, Button, Table } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const CustomerList = ({ }) => {
  const navigate = useNavigate();
  const [customers, setCustomers] = useState([]);
  const location = useLocation();
  const baseURL = process.env.REACT_APP_API_URL || '';


  const navigateToCustomer = () => {
    navigate("/admin/customers");
  };
  const navigateToEditCustomer = (id) => {
    navigate(`/admin/customer/edit/${id}`); 
  };

    const clickCustomerHandler = () => {
    navigate("/admin/customer/add");
  };

  const navigateToHome = () => {
    navigate("/admin/");
  };

  const fetchCustomers = async () => {
    try {
      const response = await fetch(`${baseURL}/api/customer/all`);
      if (!response.ok) {
        throw new Error("Failed to fetch products");
      }
      const data = await response.json();
      setCustomers(data);
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  useEffect(() => {
    fetchCustomers();
  }, []);
  const handleDelete = async (id, name) => {
    const confirmed = window.confirm(
      `Are you sure you want to delete the product "${name}"?`
    );
    if (confirmed) {
      try {
        const response = await fetch(
          `${baseURL}/api/customer/${id}`,
          {
            method: "DELETE",
          }
        );
        if (!response.ok) {
          throw new Error(`Failed to delete customer with id ${id}`);
        }
        setCustomers(customers.filter((customer) => customer.id !== id));
        toast.success(`Customer "${name}" deleted successfully!`);
      } catch (error) {
        console.error("Error deleting customer:", error);
        toast.error("Failed to delete customer.");
      }
    }
  };

  return (
    <>
      <Container className="mt-4">
        <Row>
          <Col>
            <div className="border p-4 bg-light rounded">
              <Row className="align-items-center mb-3">
                <Col>
                  <h1
                    style={{ cursor: "pointer" }}
                    onClick={navigateToCustomer}
                  >
                    Customer List
                  </h1>
                </Col>
                <Col className="text-end">
                  {location.pathname === "/admin/customers" && (
                    <Button variant="primary" onClick={clickCustomerHandler}>
                      Add Customer
                    </Button>
                  )}
                  {location.pathname === "/admin/customers" && (
                    <Button
                      variant="primary"
                      onClick={navigateToHome}
                      className="mx-2"
                    >
                      Return to home
                    </Button>
                  )}
                </Col>
              </Row>
              <Row>
                <Col>
                  <Table striped hover responsive bordered className="table-sm">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Name</th>
                        <th>Contact #</th>
                        <th>Address</th>
                        {location.pathname === "/admin/customers" && <th>Action</th>}
                      </tr>
                    </thead>
                    <tbody>
                      {customers.map((customer) => (
                        <tr key={customer.id}>
                          <td>{customer.id}</td>
                          <td>{customer.name}</td>
                          <td>{customer.contactNumber}</td>
                          <td>{customer.address}</td>
                          {location.pathname === "/admin/customers" && (
                            <>
                              <td>
                              <Button
                                variant="primary"
                                onClick={() => navigateToEditCustomer(customer.id)}
                                className="me-2"
                              >
                                Edit
                              </Button>
                              </td>
                              <td>
                              <Button
                                variant="danger"
                                onClick={() => handleDelete(customer.id, customer.name)}
                              >
                                Delete
                              </Button>
                              </td>
                            </>
                          )}
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
    
      </Container>
    </>
  );
};

export default CustomerList;
