import React, { useState, useEffect } from "react";
import { Container, Form, Button } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";

const EditProductScreen = () => {
  const { id } = useParams();
  const [product, setProduct] = useState({
    name: "",
    quantity: "",
    price: "",
    buyPrice: "", // Ensure buyPrice is included in the state
    image: "",
  });
  const navigate = useNavigate();
  const baseURL = process.env.REACT_APP_API_URL || '';

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const response = await fetch(`${baseURL}/api/product/${id}`);
        if (!response.ok) {
          throw new Error("Failed to fetch product");
        }
        const data = await response.json();
        setProduct(data);
      } catch (error) {
        console.error("Error fetching product:", error);
      }
    };

    fetchProduct();
  }, [id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    const newValue =
      name === "quantity" || name === "price" || name === "buyPrice" ? Number(value) : value;
    setProduct({ ...product, [name]: newValue });
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setProduct({ ...product, image: file });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();
      formData.append("id", id);
      formData.append("name", product.name);
      formData.append("quantity", product.quantity);
      formData.append("price", product.price);
      formData.append("buyPrice", product.buyPrice); // Ensure buyPrice is appended to form data

      if (product.image instanceof File) {
        formData.append("image", product.image);
      }

      const response = await fetch(`${baseURL}/api/product/update/${id}`, {
        method: "PUT",
        body: formData,
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log("Product updated successfully:", data);
      toast.success("Product updated successfully");
    } catch (error) {
      console.error("Error updating product:", error);
      toast.error("Error updating product");
    }
  };

  const handleBackToHome = (e) => {
    e.preventDefault();
    navigate("/admin/products");
  };

  return (
    <Container className="mt-4">
      <h1>Edit Product</h1>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="productName">
          <Form.Label>Product Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter product name"
            name="name"
            value={product.name}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="quantity">
          <Form.Label>Quantity</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter quantity"
            name="quantity"
            value={product.quantity}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="price">
          <Form.Label>Price</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter price"
            name="price"
            value={product.price}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="buyPrice">
          <Form.Label>Buy Price</Form.Label>
          <Form.Control
            type="number"
            placeholder="Enter buy price"
            name="buyPrice"
            value={product.buyPrice}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group controlId="oldImage">
          <Form.Label>Old Image</Form.Label>
          {product.image && (
            <img
              src={
                typeof product.image === "string"
                  ? `${baseURL}/api/product/image/${product.image}`
                  : URL.createObjectURL(product.image)
              }
              alt="Product"
              style={{
                maxWidth: "200px",
                maxHeight: "200px",
                display: "block",
              }}
            />
          )}
        </Form.Group>
        <Form.Group controlId="newImage">
          <Form.Label>New Image</Form.Label>
          <Form.Control
            type="file"
            accept="image/*"
            onChange={handleImageChange}
          />
        </Form.Group>
        <Button variant="primary" type="submit" className="my-2">
          Update Product
        </Button>
        <Button
          variant="primary"
          type=""
          className="text-end mx-4"
          onClick={handleBackToHome}
        >
          back to home
        </Button>
      </Form>
      <ToastContainer />
    </Container>
  );
};

export default EditProductScreen;