import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, PieChart, Pie, Cell, BarChart, Bar } from 'recharts';

const AdvancedAnalytics = () => {
    const [analyticsData, setAnalyticsData] = useState(null);
    const [products, setProducts] = useState([]);
    const baseURL = process.env.REACT_APP_API_URL || '';

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [analyticsResponse, productsResponse] = await Promise.all([
          fetch(`${baseURL}/api/reports/dashboard-metrics`),
          fetch(`${baseURL}/api/product/all`)
        ]);
        const analyticsData = await analyticsResponse.json();
        const productsData = await productsResponse.json();
        setAnalyticsData(analyticsData);
        setProducts(productsData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const productsWithProfit = products.map(product => ({
    ...product,
    profit: analyticsData.ProfitPerProduct[product.name] || 0
  }));


  if (!analyticsData) return <div>Loading...</div>;

  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884d8'];

  return (
    <Container>
      <h2 className="my-4">Advanced Analytics</h2>
      <Row>
        <Col md={6}>
          <Card className="mb-4">
            <Card.Body>
              <Card.Title>Sales Over Time</Card.Title>
              <LineChart width={500} height={300} data={analyticsData.salesOverTime}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="date" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Line type="monotone" dataKey="sales" stroke="#8884d8" />
              </LineChart>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="mb-4">
            <Card.Body>
              <Card.Title>Sales by Status</Card.Title>
              <PieChart width={400} height={300}>
                <Pie
                  data={Object.entries(analyticsData.salesByStatus).map(([key, value]) => ({ name: key, value }))}
                  dataKey="value"
                  nameKey="name"
                  cx="50%"
                  cy="50%"
                  outerRadius={100}
                  fill="#8884d8"
                  label
                >
                  {Object.entries(analyticsData.salesByStatus).map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
                <Legend />
              </PieChart>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col md={6}>
          <Card className="mb-4">
            <Card.Body>
              <Card.Title>Top Selling Products</Card.Title>
              <BarChart width={500} height={300} data={analyticsData.topSellingProducts}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="productName" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="quantity" fill="#8884d8" />
              </BarChart>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="mb-4">
            <Card.Body>
              <Card.Title>Key Metrics</Card.Title>
              <p>Average Order Value: {analyticsData.averageOrderValue.toFixed(2)}</p>
              <p>Inventory Value: {analyticsData.inventoryValue.toFixed(2)}</p>
              <p>Total Products Sold: {analyticsData.totalProductsSold}</p>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
          <Card className="mb-4">
            <Card.Body>
              <Card.Title>Profit per Product</Card.Title>
              <Table striped bordered hover>
                <thead>
                  <tr>
                    <th>Product Name</th>
                    <th>Profit</th>
                  </tr>
                </thead>
                <tbody>
                  {productsWithProfit.map((product, index) => (
                    <tr key={index}>
                      <td>{product.name}</td>
                      <td>{product.profit.toFixed(2)}</td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default AdvancedAnalytics;