import React, { useState, useEffect } from "react";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast, ToastContainer } from 'react-toastify';

const AddOrderScreen = () => {
  const [selectedCustomer, setSelectedCustomer] = useState(null);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [selectedProductId, setSelectedProductId] = useState(""); // Default to empty string
  const [orderItems, setOrderItems] = useState([]);
  const [address, setAddress] = useState("");
  const [useCustomerAddress, setUseCustomerAddress] = useState(false); // State for using customer's address
  const [customers, setCustomers] = useState([]);
  const [products, setProducts] = useState([]);
  const [selectedQuantity, setSelectedQuantity] = useState(1); // Default quantity
  const navigate = useNavigate();
  const baseURL = process.env.REACT_APP_API_URL || '';

  useEffect(() => {
    fetchCustomers();
    fetchProducts();
  }, []);

  const fetchCustomers = async () => {
    try {
      const response = await fetch(`${baseURL}/api/customer/all`);
      if (!response.ok) {
        throw new Error("Error fetching customers");
      }
      const data = await response.json();
      setCustomers(data);
    } catch (error) {
      console.error("Error fetching customers:", error);
    }
  };

  const fetchProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/all`);
      if (!response.ok) {
        throw new Error("Error fetching products");
      }
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  const handleAddProduct = () => {
    if (selectedProductId && selectedQuantity > 0) {
      const selectedProduct = products.find(
        (product) => product.id === parseInt(selectedProductId)
      );
      if (selectedProduct) {
        const existingItemIndex = orderItems.findIndex(
          (item) => item.productId === selectedProduct.id
        );
        if (existingItemIndex !== -1) {
          const updatedOrderItems = [...orderItems];
          updatedOrderItems[existingItemIndex].quantity += selectedQuantity;
          updatedOrderItems[existingItemIndex].totalPrice =
            updatedOrderItems[existingItemIndex].quantity *
            selectedProduct.price;
          setOrderItems(updatedOrderItems);
        } else {
          const newItem = {
            productId: selectedProduct.id,
            quantity: selectedQuantity,
            product: selectedProduct,
            totalPrice: selectedProduct.price * selectedQuantity,
          };
          setOrderItems([...orderItems, newItem]);
        }
        setSelectedProduct(null);
        setSelectedProductId("");
        setSelectedQuantity(1);
      }
    }
  };

  const handleRemoveItem = (index) => {
    setOrderItems(orderItems.filter((_, i) => i !== index));
  };

  const handleSubmitOrder = async () => {
    console.log(useCustomerAddress);
    try {
      const items = orderItems.map((item) => ({
        productId: item.productId,
        quantity: item.quantity,
      }));
  
      const order = {
        customerId: selectedCustomer.id,
        items: items,
        address: useCustomerAddress ? selectedCustomer.address : address,
        status: "Pending",
        lastUpdated: new Date().toISOString(), // Set to current time
      };
  
      const response = await fetch(`${baseURL}/api/orders/create`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(order),
      });
  
      if (!response.ok) {
        throw new Error("Error creating order");
      }
  
      const data = await response.json();
      console.log("Order created successfully:", data);
      toast.success("Order created successfully!");
  
      setSelectedCustomer(null);
      setOrderItems([]);
      setAddress("");
    } catch (error) {
      console.error("Error creating order:", error);
      toast.error("Error creating order");
    }
  };

  const handleBackToHome = (e) => {
    e.preventDefault();
    navigate("/admin/orders");
  };

  return (
    <div>
      <h1>Add Order</h1>

      <Form>
        <Form.Group as={Row} controlId="customerSelect">
          <Form.Label column sm={2} className="my-2">
            Select Customer:
          </Form.Label>
          <Col sm={4}>
            <Form.Control
              as="select"
              onChange={(e) => {
                const customer = customers.find(
                  (c) => c.id === parseInt(e.target.value)
                );
                setSelectedCustomer(customer);
              }}
            >
              <option value="">Select Customer</option>
              {customers.map((customer) => (
                <option key={customer.id} value={customer.id}>
                  {customer.name}
                </option>
              ))}
            </Form.Control>
          </Col>
          <Col>
            <Form.Check
              type="checkbox"
              label="Use Customer's Address"
              checked={useCustomerAddress}
              onChange={(e) => setUseCustomerAddress(e.target.checked)}
            />
          </Col>
        </Form.Group>

        <Form.Group as={Row} controlId="productSelect" className="my-2">
          <Form.Label column sm={2}>
            Select Product:
          </Form.Label>
          <Col sm={4}>
            <Form.Control
              as="select"
              value={selectedProductId}
              onChange={(e) => {
                const productId = e.target.value;
                if (productId) {
                  const product = products.find(
                    (product) => product.id === parseInt(productId)
                  );
                  setSelectedProduct(product);
                  setSelectedProductId(productId);
                } else {
                  setSelectedProduct(null);
                  setSelectedProductId("");
                }
              }}
            >
              <option value="">Select Product</option>
              {products.map((product) => (
                <option key={product.id} value={product.id}>
                  {product.name}
                </option>
              ))}
            </Form.Control>
          </Col>
          <Col sm={2}>
            <Form.Control
              type="number"
              min="1"
              max={selectedProduct ? selectedProduct.quantity : 1}
              value={selectedProduct ? selectedQuantity : ""}
              onChange={(e) => setSelectedQuantity(parseInt(e.target.value))}
              placeholder={selectedProduct ? "" : "Select product first"}
              disabled={!selectedProduct}
            />
            <Form.Text className="text-muted">
              {selectedProduct ? `Max: ${selectedProduct.quantity}` : ""}
            </Form.Text>
          </Col>
          <Col sm={2}>
            <Button
              variant="primary"
              onClick={handleAddProduct}
              disabled={!selectedProductId || selectedProduct.quantity <= 0}
            >
              Add Product
            </Button>
          </Col>
          <Form.Group controlId="address" className="my-2">
            <Form.Label>Address:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter Address"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
              disabled={useCustomerAddress}
            />
          </Form.Group>
        </Form.Group>

        <Table striped bordered hover className="my-2">
          <thead>
            <tr>
              <th>Product</th>
              <th>Quantity</th>
              <th>Total Price</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {orderItems.map((item, index) => (
              <tr key={index}>
                <td>{item.product.name}</td>
                <td>{item.quantity}</td>
                <td>{item.totalPrice}</td>
                <td>
                  <Button
                    variant="danger"
                    onClick={() => handleRemoveItem(index)}
                  >
                    Remove
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        <Col>
          <div className="text-end">
            <Button
              variant="primary"
              onClick={handleSubmitOrder}
              className="my-2"
            >
              Submit Order
            </Button>
          </div>
          <div className="text-end">
            <Button
              variant="secondary"
              onClick={handleBackToHome}
              className="my-2"
            >
              Return
            </Button>
          </div>
        </Col>
      </Form>
      <ToastContainer />
    </div>
  );
};

export default AddOrderScreen;
