import React, { useState, useEffect } from "react";
import { Container, Col, Row, Button, Table } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import { toast } from "react-toastify";

const ReportProductList = () => {
  const [products, setProducts] = useState([]);
  const navigate = useNavigate();
  const location = useLocation();
  const baseURL = process.env.REACT_APP_API_URL || '';

  const clickProductHandler = () => {
    navigate("/admin/products/add");
  };

  const navigateToProduct = () => {
    navigate("/admin/products");
  };

  const navigateToEditProduct = (id) => {
    navigate(`/admin/products/edit/${id}`); // Pass the id to the URL
  };

  const navigateToHome = () => {
    navigate("/admin");
  };

  const fetchProducts = async () => {
    try {
      const response = await fetch(`${baseURL}/api/product/low`);
      if (!response.ok) {
        throw new Error("Failed to fetch products");
      }
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      console.error("Error fetching products:", error);
      toast.error("Failed to fetch products");
    }
  };

  useEffect(() => {
    fetchProducts();
  }, []);

  const handleDelete = async (id, name) => {
    const confirmed = window.confirm(
      `Are you sure you want to delete the product "${name}"?`
    );
    if (confirmed) {
      try {
        const response = await fetch(
          `${baseURL}/api/product/${id}`,
          {
            method: "DELETE",
          }
        );
        if (!response.ok) {
          throw new Error(`Failed to delete product with id ${id}`);
        }
        setProducts(products.filter((product) => product.id !== id));
        toast.success(`Product "${name}" deleted successfully!`);
      } catch (error) {
        console.error("Error deleting product:", error);
        toast.error("Failed to delete product.");
      }
    }
  };

  return (
    <>
      <Container>
        <Row>
          <Col>
            <div className="border p-4 bg-light rounded">
              <Row className="align-items-center mb-3">
                <Col>
                  <h3>
                    Low stock products
                  </h3>
                </Col>
              </Row>
              <Row>
                <Col>
                  <Table striped bordered hover responsive className="table-sm">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Name</th>
                        <th>Quantity</th>
                      </tr>
                    </thead>
                    <tbody>
                      {products.map((product) => (
                        <tr key={product.id}>
                          <td>{product.id}</td>
                          <td
                            onClick={() => navigateToEditProduct(product.id)}
                            style={{ cursor: "pointer", color: "blue" }}
                          >
                            {product.name}
                          </td>
                          <td>{product.quantity.toString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default ReportProductList;